#search-header{
    width: 100%;
    height: 10vh;
    display: flex;
    flex-direction: column;
    margin-top: 2em;
}


#search-form{
    width: 60%;
    margin-left: 10%;
    display: flex;
    align-items: center;
    padding-bottom: 10px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.434);
}

#search-location-container input {
    font-size: 1.1em;
    border-radius: 25px;
    border: 1px solid black;
    padding: 5px 10px;
    margin: 0;
}

#search-location-container select {
    font-size: 1.1em;
    border-radius: 25px;
    border: 1px solid black;
    padding: 5px 10px;
    color: rgba(0, 0, 0, 0.497);
    margin: 0;
    margin-left: 10px;
}

#search-header button{
    font-size: 1.5em;
    border: none;
    background-color: transparent;
    display: flex;
    align-items: center;
    justify-content: center;

}

#search-bar-container{
    margin: 0;
    border: 1px solid black;
    display: flex;
    align-items: center;
    border-radius: 50%;
    padding: 5px 10px;
    width: 30px;
    height: 30px;
    justify-content: center;
    animation: contract 300ms;
    margin-right: auto;
}

#search-bar-container-expanded{
display: flex;
align-items: center;
border: 1px solid black;
height: 30px;
padding: 5px 10px;
width: 30%;
border-radius: 26px;
animation: expand 300ms;
margin-right: auto;
}

#search-bar-container-expanded input{
    width: 80%;
    height: 100%;
    margin-right: auto;
}

@keyframes contract {
    0% {
        width: 30%;
    }
    99% {
        border-radius: 26px;
        /* border-radius: 50%; */
    }
    100% {
        width: 30px;
     }
    }

@keyframes expand {
    0% {
        width: 30px;
        
     }
    100% {
        width: 30%;
        
    }
}

#search-bar-container-expanded button {
    height: 100%;
}

#search-location-container{
    margin: 0;
}

#search-bar{
border: none;
width: 0%;
}


#listing-container{
min-height: 90vh;
width: 100%;
margin-top: 5em;
}




.listing-item{
margin-left: 10%;
width: 60%; 
border-bottom: 1px solid rgba(0, 0, 0, 0.426);
display: flex;
align-items: center;
padding: 10px 10px;
}

.listing-item h4 {
    color: #9980FA;
    font-size: 1.5em;
}

.listing-item:hover{
    cursor: pointer;
    color: rgba(0, 0, 0, 0.598);
}

.listing-item-info{
margin-right: auto;
max-width: 80%;
}

.listing-item-info p {
    
white-space: nowrap;
overflow: hidden;
text-overflow: ellipsis;
max-width: 100%;

}

.listing-item-cta{
display: flex;
flex-direction: column;
align-items: flex-end;
font-size: 1.4em;
}